import "./index.scss"
import Head  from "~/components/Head";
import Foot  from "~/components/Foot";

import { Button, WhiteSpace, List ,  WingBlank} from 'antd-mobile';

import {axios} from "&";
const Item = List.Item;
const Brief = Item.Brief;
const good = {};

export default class Book extends Component{
    constructor(props){
        super(props);
        console.log(props);
        this.state = {
           
            good:{},
            goodId:props.match.params.goodId
        }
        
    }
    
    componentDidMount(){
        console.log(this.state.good)

        axios.get("/react/getBookDetail",{
            params:{
                goodId:this.state.goodId
            }
        }).then(res=>{
            this.setState({
                good:res.data.result
            })
        })
        console.log(good);
    }

    render(){
      
        
        const{
            good
        } = this.state;
        console.log(good)

        return(
            <div>
                <Head title="书籍详情" show={true}></Head>
                <div style={{background:"#fff"}}> 
                <WhiteSpace/>
                    <div style={{display:'flex' , alignItems:"center" , justifyContent:"space-around"}}>
                       <div>
                           <img style={{width:"2rem" , height:"2.6rem"}} src={good.cover} alt=""/>
                       </div>
                       <div style={{width:"60%"}} >
                           <h2 style={{height:"0.8rem" , fontSize:"0.4rem"}}>{good.title}</h2>
                           <p style={{height:"0.6rem" , fontSize:"0.3rem"}} >{good.author}</p>
                           <p style={{height:"0.6rem" , fontSize:"0.3rem"}} >{good.category}</p>
                           <p style={{height:"0.6rem" , fontSize:"0.3rem"}} >{good.status==1?"已完结":"连载中"}</p>
                       </div>
                    </div>
                    <WhiteSpace/>
                       <div className="btns" >
                            <Button type="primary" style={{width:"2.1rem" , height:"0.8rem" ,color:"#fff" ,background:"#00c98d" ,textAlign:"center" ,lineHeight:"0.8rem",footSize:"0.14rem" , border:"1px solid #00c98d"}}>开始阅读</Button><WhiteSpace />
                            <Button type="primary" style={{width:"2.1rem" , height:"0.8rem" ,color:"#00c98d" ,background:"#fff" ,textAlign:"center" ,lineHeight:"0.8rem",footSize:"0.14rem", border:"1px solid #00c98d"}} >离线下载</Button><WhiteSpace />
                            <Button type="primary" style={{width:"2.1rem" , height:"0.8rem" ,color:"#00c98d" ,background:"#fff" ,textAlign:"center" ,lineHeight:"0.8rem",footSize:"0.14rem" ,border:"1px solid #00c98d"}} >加书架</Button><WhiteSpace />
                       </div>
                       <WhiteSpace/>
                       <div>
                           <WingBlank>

                              <p style={{color:"#999999"}}>{good.desc}</p>
                           </WingBlank>
                       </div>
                       <WhiteSpace/>
                        <div>
                        <List  className="my-list">
                            <Item
                            arrow="horizontal"
                            thumb
                            multipleLine
                            onClick={() => {}}
                            >
                            最新 {good.last_chapter_name}
                            </Item>     
                        </List>
                        </div>
                        
                        <div>
                        <List  className="my-list">
                            <Item
                            arrow="horizontal"
                            thumb
                            multipleLine
                            onClick={() => {}}
                            >
                            目录 {good.last_chapter_name}
                            </Item>     
                        </List>
                        </div>
                        <div>
                            <List  className="my-list">
                                <Item >精华评论</Item>
                            </List>
                            <div style={{textAlign:"center" , background:"#fff" }}>
                               暂无评论
                            </div>

                        </div>
                    
                   

                </div>

                <Foot></Foot>
                
            </div>
        )
    }
}