
import "./index.scss"

import {NavBar,Icon} from "antd-mobile"


import {history} from "&/history";

export default class Head extends Component{

    goback=(show)=>{
        if(show){
            
            history.go(-1);
        }
    }

    gotoSearch=()=>{
        history.push("/home");
    }

    render(){
        const {
            show,
        
        }  = this.props;
        return(
            <NavBar
                className="head"
                mode="dark"
                icon={show&&<Icon style={{color:"#00c98d"}} type="left" />}
                onLeftClick={ ()=>this.goback(show) }
                rightContent={[
                    <img src={require("@/assets/images/home.png")}  onClick={this.gotoSearch}  style={{ width1:"0.4rem" , height:"0.4rem" }} />,
                  
                ]}
            >{this.props.title}</NavBar>
        )
    }
}
